import React from 'react'
import { useHistory } from 'react-router-dom'
import { Divider, Drawer, List, ListSubheader } from '@material-ui/core'
import styles from './SideMenu.module.scss'
import './SideMenu.scss'
import HomeRoundedIcon from '@material-ui/icons/HomeRounded'
import SearchRoundedIcon from '@material-ui/icons/SearchRounded'
import QueueMusicRoundedIcon from '@material-ui/icons/QueueMusicRounded'
import AddBoxRoundedIcon from '@material-ui/icons/AddBoxRounded'
import FavoriteRoundedIcon from '@material-ui/icons/FavoriteRounded'
import VolumeUpRoundedIcon from '@material-ui/icons/VolumeUpRounded'
import HistoryRoundedIcon from '@material-ui/icons/HistoryRounded'
import MenuItem from './MenuItem'
import { RootState } from '../../../app/reducer'
import { useSelector } from 'react-redux'

type SideMenuProps = {
  isOpen: boolean
}

const SideMenu = ({ isOpen }: SideMenuProps) => {
  const [selectedIndex, setSelectedIndex] = React.useState(0)
  const handleListItemClick = (index: React.SetStateAction<number>) => {
    setSelectedIndex(index)
  }

  const history = useHistory()

  const playlists = useSelector((state: RootState) => [
    ...state.mediaLibrary.dynamic,
    ...state.mediaLibrary.static,
  ])

  const onHomeClick = () => {
    handleListItemClick(0)
    history.push('/')
  }

  const onSearchClick = () => {
    handleListItemClick(1)
    history.push('/search')
  }

  const onPlaylistClick = () => {
    history.push('/playlist')
  }

  const onNowPlayingClick = () => {
    history.push('/now-playing')
  }

  const onMediaCollectionClick = () => {
    handleListItemClick(2)
    history.push('/collection')
  }

  return (
    <Drawer
      variant={'permanent'}
      classes={{
        paper: styles.Menu,
      }}
    >
      <List>
        <MenuItem
          isOpen={isOpen}
          isSelected={selectedIndex === 0}
          onClick={onHomeClick}
          text={'Главная'}
        >
          <HomeRoundedIcon />
        </MenuItem>
        <MenuItem
          isOpen={isOpen}
          isSelected={selectedIndex === 1}
          onClick={onSearchClick}
          text={'Поиск'}
        >
          <SearchRoundedIcon />
        </MenuItem>
        <MenuItem
          isOpen={isOpen}
          isSelected={selectedIndex === 2}
          onClick={onMediaCollectionClick}
          text={'Моя медиатека'}
        >
          <QueueMusicRoundedIcon />
        </MenuItem>
      </List>
      <List
        style={{ marginTop: '24px' }}
        subheader={
          <ListSubheader
            className={styles.SubHeader}
            classes={{ root: 'ellipsis-one-line' }}
          >
            ПЛЕЙЛИСТЫ
          </ListSubheader>
        }
      >
        <MenuItem
          isSelected={false}
          isOpen={isOpen}
          onClick={() => {}}
          text={'Создать плейлист'}
        >
          <AddBoxRoundedIcon />
        </MenuItem>
        <MenuItem
          isSelected={selectedIndex === 3}
          isOpen={isOpen}
          text={'Любимые треки'}
          onClick={() => {
            setSelectedIndex(3)
            onPlaylistClick()
          }}
        >
          <FavoriteRoundedIcon />
        </MenuItem>
        <MenuItem
          isSelected={selectedIndex === 4}
          isOpen={isOpen}
          text={'Проигрываемые'}
          onClick={() => {
            setSelectedIndex(4)
            onNowPlayingClick()
          }}
        >
          <VolumeUpRoundedIcon />
        </MenuItem>
        <MenuItem
          isSelected={selectedIndex === 5}
          isOpen={isOpen}
          text={'Недавнее'}
          onClick={() => {
            setSelectedIndex(5)
          }}
        >
          <HistoryRoundedIcon />
        </MenuItem>
        <Divider />
        <>
          {playlists.map((el, idx) => {
            return (
              <MenuItem
                isSelected={selectedIndex === idx + 100}
                isOpen={isOpen}
                text={el.title}
                onClick={() => {
                  setSelectedIndex(idx + 100)
                }}
              >
                <img
                  src={el.thumbnail}
                  alt={el.title}
                  height={35}
                  width={35}
                  style={{
                    objectFit: 'cover',
                    borderRadius: '50%',
                    border: '1px solid #b3b3b3',
                    marginLeft: '-5px',
                  }}
                />
              </MenuItem>
            )
          })}
        </>
      </List>
    </Drawer>
  )
}

export default SideMenu
