import React from 'react'
import { Route, Switch } from 'react-router-dom'
import NowPlayingPlaylist from '../playlist/NowPlayingPlaylist'
import SearchContent from '../search/SearchContent'
import MediaLibraryContent from '../mediaLibrary/MediaLibraryContent'

const ContentSwitcher = () => {
  return (
    <Switch>
      <Route path="/collection">
        <MediaLibraryContent />
      </Route>
      <Route path="/playlist">
        <NowPlayingPlaylist />
      </Route>
      <Route path="/search/:q">
        <SearchContent />
      </Route>
      <Route path="/search">
        <SearchContent />
      </Route>
      <Route path="/now-playing">
        <NowPlayingPlaylist />
      </Route>
      <Route path="/">
        <NowPlayingPlaylist />
      </Route>
    </Switch>
  )
}

export default ContentSwitcher
