import React, { useEffect } from 'react'

import { useLocation, useParams } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { search, searchNext } from './searchSlice'
import { RootState } from '../../app/reducer'
import { Paper } from '@material-ui/core'
import { setAudio, setPlaylist } from '../player/playerSlice'
import { Audio, Playlist } from '../player/types'
import { Playlist as PlaylistT } from './types'

const SearchContent = () => {
  const { q } = useParams()
  const dispatch = useDispatch()
  const searchResultInput = useSelector(
    (state: RootState) => state.search.searchResult.searchInput
  )

  useEffect(() => {
    if (q) {
      if (decodeURIComponent(q) !== searchResultInput)
        dispatch(search(decodeURIComponent(q)))
    }
  }, [q, searchResultInput])

  const searchResult = useSelector(
    (state: RootState) => state.search.searchResult
  )

  const currentPlaylistId = useSelector(
    (state: RootState) => state.player.playlist.id
  )

  const onAudioClick = (audio: Audio) => {
    if (currentPlaylistId !== `/search/${q}`) {
      const newPlaylist: Playlist = {
        id: `/search/${q}`,
        url: `/search/${q}`,
        name: `Поиск: ${q}`,
        audios: searchResult.searchArr.filter(
          (el) => el.type === 'audio'
        ) as Audio[],
      }
      dispatch(setPlaylist({ playlist: newPlaylist, audio }))
    } else {
      dispatch(setAudio(audio as Audio))
    }
  }

  const onPlaylistClick = (playlist: PlaylistT) => {
    console.log(playlist)
  }

  let location = useLocation()
  const nextPageUrl = useSelector(
    (state: RootState) => state.search.searchResult.nextPageUrl
  )

  const isFetching = useSelector((state: RootState) => state.search.isFetching)

  useEffect(() => {
    let timer: number | undefined

    const el = document.getElementById('ContentWrapper')
    if (el) {
      el.onscroll = (event) => {
        if (timer) {
          window.clearTimeout(timer)
        }

        timer = window.setTimeout(function () {
          // Preload music
          const spacePreload = 50

          const toBottom = el.scrollHeight - el.clientHeight - el.scrollTop
          // console.log(toBottom)
          if (toBottom < spacePreload) {
            if (nextPageUrl && !isFetching) {
              dispatch(searchNext(nextPageUrl))
            }
          }
        }, 100)
      }
    }
  }, [nextPageUrl, isFetching, location.pathname])

  return (
    <div>
      {searchResult.searchArr.map((el) => {
        switch (el.type) {
          case 'audio': {
            return <AudioResult key={el.id} audio={el} onClick={onAudioClick} />
          }
          case 'playlist': {
            return (
              <PlaylistResult
                key={el.id}
                playlist={el}
                onClick={onPlaylistClick}
              />
            )
          }
          case 'channel': {
            return <></>
          }
          default: {
            return <></>
          }
        }
      })}
    </div>
  )
}

type AudioResultProps = {
  audio: Audio
  onClick: (audio: Audio) => void
}
const AudioResult = ({ audio, onClick }: AudioResultProps) => {
  return (
    <Paper
      style={{
        textAlign: 'left',
        margin: '5px',
        display: 'flex',
        alignItems: 'center',
        maxWidth: '1955px',
        overflow: 'hidden',
      }}
      key={audio.id}
      onClick={() => {
        onClick(audio)
      }}
    >
      <img
        style={{ objectFit: 'cover' }}
        src={audio.thumbnail}
        width={100}
        height={64}
        alt={audio.name}
      />
      <div
        style={{
          display: 'flex',
          flexWrap: 'wrap',
          overflow: 'hidden',
        }}
      >
        <div
          style={{
            paddingLeft: '5px',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
            overflow: 'hidden',
            width: '100%',
          }}
        >
          {audio.name}
        </div>
        <div
          style={{
            paddingLeft: '5px',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
            overflow: 'hidden',
            width: '100%',
            fontSize: '12px',
            lineHeight: '16px',
            letterSpacing: '0.015em',
            color: '#d9d8d8',
          }}
        >
          {audio.artist}
        </div>
      </div>
    </Paper>
  )
}

type PlaylistResultProps = {
  playlist: PlaylistT
  onClick: (playlist: PlaylistT) => void
}
const PlaylistResult = ({ playlist, onClick }: PlaylistResultProps) => {
  return (
    <Paper
      style={{
        textAlign: 'left',
        margin: '5px',
        display: 'flex',
        alignItems: 'center',
        maxWidth: '1955px',
        overflow: 'hidden',
      }}
      key={playlist.id}
      onClick={() => {
        onClick(playlist)
      }}
    >
      <img
        style={{ objectFit: 'cover' }}
        src={playlist.thumbnail}
        width={100}
        height={64}
        alt={playlist.name}
      />
      <div
        style={{
          display: 'flex',
          flexWrap: 'wrap',
          overflow: 'hidden',
        }}
      >
        <div
          style={{
            paddingLeft: '5px',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
            overflow: 'hidden',
            width: '100%',
          }}
        >
          {playlist.name}
        </div>
        <div
          style={{
            paddingLeft: '5px',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
            overflow: 'hidden',
            width: '100%',
            fontSize: '12px',
            lineHeight: '16px',
            letterSpacing: '0.015em',
            color: '#d9d8d8',
          }}
        >
          {playlist.size} Аудио
        </div>
      </div>
    </Paper>
  )
}

export default SearchContent
