import React, { useEffect, useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../../../app/reducer'
import { setIsVideo, setPlaying } from '../playerSlice'
import ReactPlayer from 'react-player'

const VideoPlayer = () => {
  const videoPlayerRef = useRef(null)

  const dispatch = useDispatch()

  useEffect(() => {
    if (videoPlayerRef.current) {
      dispatch(setIsVideo(true))
      return () => {
        dispatch(setIsVideo(false))
      }
    }
  }, [videoPlayerRef, dispatch])

  useEffect(() => {
    const player = document.getElementById('player')
    let timerId: NodeJS.Timeout | null = null
    if (videoPlayerRef.current) {
      timerId = setInterval(() => {
        let diff = Math.abs(
          // @ts-ignore
          player.currentTime - videoPlayerRef.current.getCurrentTime()
        )
        if (diff > 0.5) {
          // @ts-ignore
          videoPlayerRef.current.seekTo(player.currentTime + 0.5, 'seconds')
        }
      }, 1000)
    }
    return () => {
      if (timerId) {
        clearInterval(timerId)
      }
    }
  }, [videoPlayerRef])

  const currentAudioID = useSelector(
    (state: RootState) =>
      'https://www.youtube.com/watch?v=' + state.player.currentAudio.id
  )
  const isPlaying = useSelector((state: RootState) => state.player.isPlaying)

  const onPause = () => {
    dispatch(setPlaying(false))
  }
  const onPlay = () => {
    dispatch(setPlaying(true))
  }

  return (
    <ReactPlayer
      controls={false}
      pip={true}
      id={'VideoPlayer'}
      ref={videoPlayerRef}
      url={currentAudioID}
      style={{
        height: '100%',
        // maxWidth: '1050px',
        flex: '1 0 auto',
        position: 'relative',
      }}
      width="100%"
      height="100%"
      volume={0}
      playing={isPlaying}
      onPause={onPause}
      onPlay={onPlay}
    />
  )
}

export default VideoPlayer
