import React from 'react'
import { Audio } from '../player/types'
import { useDispatch, useSelector } from 'react-redux'
import { setAudio } from '../player/playerSlice'
import { RootState } from '../../app/reducer'
import styles from './AudioCell.module.scss'
import { Col } from 'antd'
import clsx from 'clsx'

type AudioCellProps = {
  audio: Audio
}
const AudioCell = ({ audio }: AudioCellProps) => {
  const dispatch = useDispatch()
  const onButtonClick = () => {
    dispatch(setAudio(audio))
  }

  const isPlaying = useSelector(
    (state: RootState) =>
      state.player.isPlaying && audio.id === state.player.currentAudio.id
  )

  let wrapperClasses = [styles.ImageWrapper]
  let imageClasses = [styles.Image]
  let buttonClasses = [styles.PlayButton]
  if (isPlaying) {
    wrapperClasses = [...wrapperClasses, styles.ImageWrapperActive]
    imageClasses = [...imageClasses, styles.ImageActive]
    buttonClasses = [...buttonClasses, styles.PlayButtonActive]
  }

  return (
    <Col style={{ color: 'rgb(179, 179, 179)' }} xs={12} md={8} lg={6} xxl={4}>
      <div style={{ padding: '10px', overflow: 'hidden' }}>
        <div className={clsx(wrapperClasses)}>
          <PlayButton
            className={clsx(buttonClasses)}
            onClick={onButtonClick}
            isPlaying={isPlaying}
          />
          <img
            alt={''}
            className={clsx(imageClasses)}
            src={audio.thumbnail}
            draggable={false}
          />
        </div>
      </div>
      <div style={{ paddingLeft: '10px', paddingRight: '10px' }}>
        {audio.name}
      </div>
    </Col>
  )
}

type PlayButtonProps = {
  isPlaying: boolean
  onClick: () => void
  className: string
}
const PlayButton = ({ isPlaying, onClick, className }: PlayButtonProps) => {
  return (
    <div className={className} onClick={onClick}>
      <div style={{ userSelect: 'none' }}>
        {isPlaying ? (
          <img src={require('./LissNight.png')} width={50} height={50} />
        ) : (
          '|>'
        )}
      </div>
    </div>
  )
}

export default AudioCell
