import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import AuthApi, { LoginDetails } from '../../api/AuthApi'
import { AppDispatch, AppThunk } from '../../app/store'

type Status = 'authorized' | 'unauthorized'

type Auth = {
  username: string
  status: Status
  isLoading: boolean
}

const initialState: Auth = {
  username: '',
  status: 'unauthorized',
  isLoading: false,
}

const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    loginRequest(state) {
      state.isLoading = true
    },
    loginFinished(state, action: PayloadAction<Status>) {
      state.isLoading = false
      state.status = action.payload
    },
    logout(state) {
      state.status = 'unauthorized'
    },
  },
})

export const login = (loginDetails: LoginDetails): AppThunk => async (
  dispatch: AppDispatch
) => {
  dispatch(authSlice.actions.loginRequest())
  AuthApi.login(loginDetails)
    .then(() => {
      dispatch(authSlice.actions.loginFinished('authorized'))
    })
    .catch(() => {
      dispatch(authSlice.actions.loginFinished('unauthorized'))
    })
}

export const logout = (): AppThunk => async (dispatch: AppDispatch) => {
  dispatch(authSlice.actions.logout())
  AuthApi.logout().then(null)
}

export default authSlice.reducer
