import React from 'react'
import { Button, createStyles, Dialog, Slide, Theme } from '@material-ui/core'
import { TransitionProps } from '@material-ui/core/transitions'
import { makeStyles } from '@material-ui/core/styles'
import PlayerFullScreenLayout from './playerFullScreen/PlayerFullScreenLayout'
import { RootState } from '../../app/reducer'
import { useDispatch, useSelector } from 'react-redux'
import { setIsOverlayOpen } from './playerSlice'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    appBar: {
      position: 'relative',
    },
    title: {
      marginLeft: theme.spacing(2),
      flex: 1,
    },
  })
)

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & { children?: React.ReactElement },
  ref: React.Ref<unknown>
) {
  return <Slide direction="up" ref={ref} {...props} />
})

const PlayerOverlay = () => {
  const isOverlayOpen = useSelector(
    (state: RootState) => state.player.isOverlayOpen
  )
  const dispatch = useDispatch()

  const handleClose = () => {
    dispatch(setIsOverlayOpen(false))
  }

  return (
    <Dialog
      fullScreen
      open={isOverlayOpen}
      onClose={handleClose}
      TransitionComponent={Transition}
    >
      <PlayerFullScreenLayout isOverlay={true} />
    </Dialog>
  )
}

export default PlayerOverlay
