import React, { useEffect, useState } from 'react'
import { Layout } from 'antd'
import {
  Grid,
  IconButton,
  isWidthUp,
  Typography,
  withWidth,
} from '@material-ui/core'
import KeyboardArrowDownRoundedIcon from '@material-ui/icons/KeyboardArrowDownRounded'
import FullscreenRoundedIcon from '@material-ui/icons/FullscreenRounded'
import MoreVertRoundedIcon from '@material-ui/icons/MoreVertRounded'
import { useHistory } from 'react-router-dom'
import { RootState } from '../../../app/reducer'
import { useDispatch, useSelector } from 'react-redux'
import PrevButton from '../buttons/PrevButton'
import PlayButton from '../buttons/PlayButton'
import NextButton from '../buttons/NextButton'
import { TimeControl } from '../playerDefault/desktopPlayer/DesktopPlayerControls'
import ShuffleButton from '../buttons/ShuffleButton'
import LoopButton from '../buttons/LoopButton'
import VideoPlayer from '../videoPlayer/VideoPlayer'
import { ScreenWidth } from '../../layout/MainPageLayout'
import { setIsOverlayOpen } from '../playerSlice'

type PlayerFullScreenLayoutProps = {
  isOverlay?: boolean
}

const PlayerFullScreenLayout = ({
  isOverlay = false,
}: PlayerFullScreenLayoutProps) => {
  const currentAudio = useSelector(
    (state: RootState) => state.player.currentAudio
  )

  return (
    <Layout
      style={{
        height: '100%',
        minHeight: '-webkit-fill-available',
        maxHeight: '-webkit-fill-available',
        color: 'white',
      }}
    >
      <PlayerHeader isOverlay={isOverlay} />
      <PlayerContent
        imageSrc={currentAudio.thumbnail}
        title={currentAudio.name}
      />
      <PlayerFooter />
    </Layout>
  )
}

type PlayerHeaderProps = {
  isOverlay: boolean
}
const PlayerHeader = ({ isOverlay }: PlayerHeaderProps) => {
  const playlistTitle = useSelector(
    (state: RootState) => state.player.playlist.name
  )

  const toggleFullScreen = () => {
    let elem = document.getElementById('VideoPlayer')
    if (elem) {
      if (!document.fullscreenElement) {
        elem.requestFullscreen().catch((err) => {
          alert(
            `Error attempting to enable full-screen mode: ${err.message} (${err.name})`
          )
        })
      } else {
        document.exitFullscreen()
      }
    }
  }

  const dispatch = useDispatch()
  const history = useHistory()

  const isOverlayOpen = useSelector(
    (state: RootState) => state.player.isOverlayOpen
  )

  useEffect(() => {
    history.location.state = { isOpen: true }
  }, [history])

  const onNowPlayingClick = () => {
    if (isOverlay) {
      dispatch(setIsOverlayOpen(false))
    }
    history.push('/now-playing')
  }

  return (
    <Layout.Header
      className={'dev'}
      style={{
        display: 'flex',
        padding: 0,
        justifyContent: 'space-between',
        backgroundColor: '#121212',
      }}
    >
      <BackButton isHistory={!isOverlayOpen} />
      <div
        onClick={onNowPlayingClick}
        style={{
          cursor: 'pointer',
          userSelect: 'none',
          color: 'white',
        }}
      >
        <Typography
          variant={'subtitle1'}
          style={{ color: 'white', fontSize: '0.75rem', textAlign: 'center' }}
        >
          Плейлист
        </Typography>
        <Typography
          variant={'h6'}
          style={{ color: 'white', fontSize: '1rem', textAlign: 'center' }}
        >
          {playlistTitle}
        </Typography>
      </div>
      <div>
        <IconButton>
          <FullscreenRoundedIcon
            fontSize={'large'}
            onClick={toggleFullScreen}
          />
        </IconButton>
        <IconButton>
          <MoreVertRoundedIcon fontSize={'large'} />
        </IconButton>
      </div>
    </Layout.Header>
  )
}
type PlayerContentProps = {
  imageSrc: string
  title: string
  width: ScreenWidth
}
const PlayerContent = withWidth()(
  ({ imageSrc, title, width }: PlayerContentProps) => {
    return (
      <Layout.Content className={'dev'} style={{ backgroundColor: 'black' }}>
        <div
          className={'dev'}
          style={{
            height: 'calc(100% - 120px)',
            backgroundColor: 'black',
          }}
        >
          {/*<img*/}
          {/*  src={imageSrc}*/}
          {/*  style={{ objectFit: 'cover', maxWidth: '100%', maxHeight: '100%' }}*/}
          {/*/>*/}
          <VideoPlayer />
        </div>
        <div
          className={'dev'}
          style={{
            height: isWidthUp('sm', width) ? '60px' : '120px',
          }}
        >
          <Typography variant={'h6'} color={'primary'}>
            {title}
          </Typography>
        </div>
      </Layout.Content>
    )
  }
)

type FooterProps = {
  width: ScreenWidth
}
const PlayerFooter = withWidth()(({ width }: FooterProps) => {
  return (
    <Layout.Footer
      className={'dev'}
      style={{ padding: 0, backgroundColor: 'black' }}
    >
      <div
        className={'dev'}
        style={{
          height: isWidthUp('sm', width) ? '30px' : '60px',
          paddingLeft: '20px',
          paddingRight: '20px',
        }}
      >
        <TimeControl />
      </div>
      <div
        className={'dev'}
        style={{ height: isWidthUp('sm', width) ? '80px' : '140px' }}
      >
        <Grid
          item
          container
          justify={'space-around'}
          style={{ maxWidth: '320px' }}
          alignItems={'center'}
        >
          <ShuffleButton size={'large'} />
          <PrevButton size={'large'} />
          <PlayButton size={'large'} />
          <NextButton size={'large'} />
          <LoopButton size={'large'} />
        </Grid>
      </div>
    </Layout.Footer>
  )
})

type BackButtonProps = {
  isHistory?: boolean
}

export const BackButton = ({ isHistory = true }: BackButtonProps) => {
  const history = useHistory()

  const dispatch = useDispatch()

  const [isHistoryBack, setIsHistoryBack] = useState(true)

  useEffect(() => {
    if (!isHistory) {
      window.onpopstate = (event: PopStateEvent) => {
        event.preventDefault()
        // @ts-ignore
        setIsHistoryBack(true)
        dispatch(setIsOverlayOpen(false))
      }
      return () => {
        window.onpopstate = () => {}
      }
    }
  }, [isHistory, isHistoryBack])

  const onBack = () => {
    if (isHistory) {
      if (history.length > 2) {
        history.goBack()
      } else {
        history.push('/')
      }
    } else {
      setIsHistoryBack(false)
      dispatch(setIsOverlayOpen(false))
    }
  }

  useEffect(() => {
    if (isHistoryBack) {
      return () => {
        history.goForward()
      }
    } else {
    }
  }, [isHistoryBack])

  return (
    <IconButton onClick={onBack}>
      <KeyboardArrowDownRoundedIcon fontSize={'large'} />
    </IconButton>
  )
}

export default PlayerFullScreenLayout
